import React from 'react';
import Icon from '../../../components/AppIcon';

const CaseStudyApproach = ({ practiceArea }) => {
  const caseStudyData = {
    civil: {
      title: "Nossa Metodologia em Ao",
      subtitle: "Casos reais demonstram nossa abordagem estratgica",
      cases: [
        {
          icon: "FileText",
          category: "Responsabilidade Civil",
          title: "Indenizao por Danos Morais",
          scenario: "Cliente teve o nome negativado indevidamente por empresa de telecomunicaes aps quitar dbito.",
          approach: [
            "Anlise detalhada da documentao de pagamento",
            "Notificao extrajudicial para resoluo amigvel",
            "Ao judicial com pedido de tutela antecipada",
            "Produo de provas da negativao indevida"
          ],
          outcome: "Resoluo em 8 meses com indenizao de R$ 12.000 e excluso imediata do nome dos rgos de proteo.",
          methodology: "Estratgia preventiva com foco na resoluo mais rpida possvel"
        },
        {
          icon: "Home",
          category: "Direitos Reais",
          title: "Regularizao de Propriedade",
          scenario: "Famlia ocupava terreno h 20 anos sem escritura, enfrentando ameaa de despejo.",
          approach: [
            "Levantamento histrico da ocupao",
            "Coleta de testemunhas e documentos",
            "Anlise da cadeia dominial do imvel",
            "Ao de usucapio com pedido liminar"
          ],
          outcome: "Reconhecimento da propriedade em 18 meses, garantindo segurana jurdica  famlia.",
          methodology: "Abordagem tcnica com sensibilidade social"
        }
      ]
    },
    consumer: {
      title: "Defesa Efetiva do Consumidor",
      subtitle: "Resultados concretos na proteo dos seus direitos",
      cases: [
        {
          icon: "CreditCard",
          category: "Servios Bancrios",
          title: "Reviso de Contrato Bancrio",
          scenario: "Empresrio descobriu cobrana de juros abusivos em financiamento de veculo comercial.",
          approach: [
            "Auditoria completa do contrato de financiamento",
            "Clculo dos valores cobrados indevidamente",
            "Tentativa de acordo extrajudicial",
            "Ao revisional com pedido de consignao"
          ],
          outcome: "Reduo de 40% no valor das parcelas e restituio de R$ 35.000 pagos a mais.",
          methodology: "Anlise tcnica especializada com negociao estratgica"
        },
        {
          icon: "Plane",
          category: "Direito do Turismo",
          title: "Voo Cancelado em Viagem Familiar",
          scenario: "Famlia perdeu conexo internacional por atraso de 6 horas em voo domstico.",
          approach: [
            "Documentao completa dos prejuzos",
            "Protocolo administrativo na companhia area",
            "Ao judicial por danos morais e materiais",
            "Produo de provas dos gastos extras"
          ],
          outcome: "Indenizao de R$ 25.000 por danos morais e R$ 8.000 por danos materiais.",
          methodology: "Atuao rpida com foco na reparao integral"
        }
      ]
    },
    family: {
      title: "Cuidado Familiar com Resultados",
      subtitle: "Solues sensveis para questes delicadas",
      cases: [
        {
          icon: "Heart",
          category: "Divrcio Consensual",
          title: "Separao Amigvel com Filhos",
          scenario: "Casal decidiu se separar amigavelmente, mas precisava regularizar guarda e penso dos dois filhos.",
          approach: [
            "Mediao familiar para acordos equilibrados",
            "Elaborao de acordo de guarda compartilhada",
            "Clculo justo da penso alimentcia",
            "Homologao judicial em processo consensual"
          ],
          outcome: "Divrcio concludo em 45 dias com acordo satisfatrio para ambas as partes.",
          methodology: "Mediao humanizada priorizando o bem-estar das crianas"
        },
        {
          icon: "Baby",
          category: "Guarda e Convivncia",
          title: "Modificao de Guarda",
          scenario: "Pai buscava modificar guarda unilateral para compartilhada aps mudana de vida profissional.",
          approach: [
            "Avaliao psicossocial da nova situao",
            "Demonstrao da estabilidade familiar",
            "Negociao com a me das crianas",
            "Pedido judicial fundamentado no melhor interesse"
          ],
          outcome: "Guarda compartilhada estabelecida com convivncia equilibrada e reduo da penso.",
          methodology: "Foco no dilogo e no bem-estar infantil"
        }
      ]
    },
    business: {
      title: "Estratgia Empresarial Preventiva",
      subtitle: "Protegendo negcios com viso de futuro",
      cases: [
        {
          icon: "Building2",
          category: "Recuperao Judicial",
          title: "Reestruturao de Empresa Familiar",
          scenario: "Empresa de construo civil com 50 funcionrios enfrentava crise financeira severa.",
          approach: [
            "Anlise completa da situao financeira",
            "Elaborao de plano de recuperao vivel",
            "Negociao com credores e fornecedores",
            "Acompanhamento da execuo do plano"
          ],
          outcome: "Empresa recuperada em 24 meses, mantendo empregos e retomando crescimento.",
          methodology: "Viso estratgica com responsabilidade social"
        },
        {
          icon: "FileCheck",
          category: "Contratos Comerciais",
          title: "Estruturao de Parceria Comercial",
          scenario: "Duas empresas queriam formar joint venture para projeto de grande porte.",
          approach: [
            "Estruturao jurdica da parceria",
            "Elaborao de contratos detalhados",
            "Definio de governana corporativa",
            "Implementao de compliance especfico"
          ],
          outcome: "Parceria bem-sucedida com projeto executado sem conflitos jurdicos.",
          methodology: "Planejamento preventivo com foco na sustentabilidade"
        }
      ]
    }
  };

  const data = caseStudyData?.[practiceArea] || caseStudyData?.civil;

  return (
    <section className="py-20 bg-white">
      <div className="max-w-7xl mx-auto px-4 lg:px-6">
        {/* Header */}
        <div className="text-center mb-16">
          <h2 className="font-lora font-bold text-3xl lg:text-4xl text-slate-900 mb-4">
            {data?.title}
          </h2>
          <p className="font-inter text-lg text-slate-600 max-w-3xl mx-auto">
            {data?.subtitle}
          </p>
        </div>

        {/* Cases */}
        <div className="space-y-12 lg:space-y-16">
          {data?.cases?.map((caseStudy, index) => (
            <div 
              key={index}
              className={`grid lg:grid-cols-2 gap-8 lg:gap-12 items-center ${
                index % 2 === 1 ? 'lg:grid-flow-col-dense' : ''
              }`}
            >
              {/* Content */}
              <div className={index % 2 === 1 ? 'lg:col-start-2' : ''}>
                <div className="flex items-center space-x-3 mb-4">
                  <div className="flex items-center justify-center w-10 h-10 bg-indigo-100 rounded-lg">
                    <Icon name={caseStudy?.icon} size={20} className="text-indigo-600" />
                  </div>
                  <span className="font-inter font-medium text-sm text-indigo-600 uppercase tracking-wider">
                    {caseStudy?.category}
                  </span>
                </div>

                <h3 className="font-lora font-semibold text-2xl text-slate-900 mb-4">
                  {caseStudy?.title}
                </h3>

                <div className="bg-slate-50 rounded-lg p-6 mb-6">
                  <h4 className="font-inter font-semibold text-slate-900 mb-2">Situao:</h4>
                  <p className="font-inter text-slate-700 leading-relaxed">
                    {caseStudy?.scenario}
                  </p>
                </div>

                <div className="mb-6">
                  <h4 className="font-inter font-semibold text-slate-900 mb-3">Nossa Abordagem:</h4>
                  <div className="space-y-2">
                    {caseStudy?.approach?.map((step, stepIndex) => (
                      <div key={stepIndex} className="flex items-start space-x-3">
                        <div className="flex items-center justify-center w-6 h-6 bg-indigo-100 rounded-full flex-shrink-0 mt-0.5">
                          <span className="text-indigo-600 font-semibold text-xs">
                            {stepIndex + 1}
                          </span>
                        </div>
                        <span className="font-inter text-slate-700 text-sm">
                          {step}
                        </span>
                      </div>
                    ))}
                  </div>
                </div>

                <div className="bg-green-50 border border-green-200 rounded-lg p-6">
                  <h4 className="font-inter font-semibold text-green-900 mb-2 flex items-center">
                    <Icon name="CheckCircle" size={18} className="text-green-600 mr-2" />
                    Resultado:
                  </h4>
                  <p className="font-inter text-green-800 leading-relaxed mb-3">
                    {caseStudy?.outcome}
                  </p>
                  <div className="flex items-center space-x-2">
                    <Icon name="Target" size={16} className="text-green-600" />
                    <span className="font-inter text-sm text-green-700 font-medium">
                      {caseStudy?.methodology}
                    </span>
                  </div>
                </div>
              </div>

              {/* Visual */}
              <div className={index % 2 === 1 ? 'lg:col-start-1' : ''}>
                <div className="relative">
                  <div className="bg-gradient-to-br from-slate-100 to-slate-200 rounded-2xl p-8 lg:p-12">
                    <div className="text-center">
                      <div className="flex items-center justify-center w-20 h-20 bg-white rounded-full shadow-lg mb-6 mx-auto">
                        <Icon name={caseStudy?.icon} size={32} className="text-indigo-600" />
                      </div>
                      <h4 className="font-lora font-semibold text-xl text-slate-900 mb-4">
                        Metodologia Aplicada
                      </h4>
                      <div className="space-y-4">
                        <div className="flex items-center justify-center space-x-2">
                          <Icon name="Search" size={16} className="text-slate-600" />
                          <span className="font-inter text-sm text-slate-700">Anlise Detalhada</span>
                        </div>
                        <div className="flex items-center justify-center space-x-2">
                          <Icon name="Users" size={16} className="text-slate-600" />
                          <span className="font-inter text-sm text-slate-700">Abordagem Humanizada</span>
                        </div>
                        <div className="flex items-center justify-center space-x-2">
                          <Icon name="Target" size={16} className="text-slate-600" />
                          <span className="font-inter text-sm text-slate-700">Resultado Efetivo</span>
                        </div>
                      </div>
                    </div>
                  </div>

                  {/* Decorative Elements */}
                  <div className="absolute -top-4 -right-4 w-8 h-8 bg-indigo-200 rounded-full opacity-60"></div>
                  <div className="absolute -bottom-4 -left-4 w-6 h-6 bg-amber-200 rounded-full opacity-60"></div>
                </div>
              </div>
            </div>
          ))}
        </div>

        {/* Disclaimer */}
        <div className="mt-16 p-6 bg-slate-100 rounded-xl border border-slate-200">
          <div className="flex items-start space-x-3">
            <Icon name="Info" size={20} className="text-slate-600 flex-shrink-0 mt-0.5" />
            <div>
              <h4 className="font-inter font-semibold text-slate-900 mb-2">
                Importante sobre os Casos Apresentados
              </h4>
              <p className="font-inter text-sm text-slate-700 leading-relaxed">
                Os casos apresentados so baseados em situaes reais, mas foram anonimizados e generalizados para fins educativos, 
                em conformidade com o sigilo profissional e as normas da OAB. Cada caso  nico e os resultados podem variar 
                conforme as circunstncias especficas. No h garantia de resultados similares em outros processos.
              </p>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default CaseStudyApproach;