import React from 'react';
import Icon from '../../../components/AppIcon';

const ProfessionalJourney = () => {
  const milestones = [
    {
      year: "2005",
      title: "Incio na OAB como Estagirio",
      description: "Inscrio como estagirio na OAB/GO n 17.604/E, realizando atos jurdicos e peticionamento.",
      icon: "FileSignature",
      color: "bg-emerald-500"
    },
    {
      year: "2006",
      title: "Graduao em Direito",
      description: "Formado em Direito pelo Centro Universitrio de Gois, em Goinia-GO.",
      icon: "GraduationCap",
      color: "bg-blue-500"
    },
    {
      year: "2008",
      title: "Inscrio como Advogado",
      description: "OAB/GO n 27.669, com atuao em cerca de 2.500 processos pela Procuradoria de Assistncia Judiciria do Estado de Gois.",
      icon: "Gavel",
      color: "bg-purple-500"
    },
    {
      year: "2010",
      title: "Transferncia para OAB/MT",
      description: "Inscrio por transferncia na OAB/MT n 14.159-B.",
      icon: "MapPin",
      color: "bg-orange-500"
    },
    {
      year: "2011",
      title: "Analista Jurdico TRF1",
      description: "Aprovao no concurso de Analista Jurdico do Tribunal Regional Federal da 1 Regio.",
      icon: "Landmark",
      color: "bg-amber-500"
    },
    {
      year: "2012",
      title: "Ps-Graduao em Direito Pblico",
      description: "Especializao em Direito Pblico Material pelo Centro Universitrio de Araras-MG.",
      icon: "BookOpenCheck",
      color: "bg-indigo-500"
    },
    {
      year: "2012",
      title: "Incio na Docncia",
      description: "Atuao como professor de Direito Civil, Processual Civil, Empresarial e Trabalhista.",
      icon: "Users",
      color: "bg-teal-500"
    },
    {
      year: "2016",
      title: "Inovao Digital",
      description: "Implementao de virtualizao de processos e solues digitais.",
      icon: "Laptop",
      color: "bg-pink-500"
    },
    {
      year: "2018",
      title: "Procurador em Alto Garas-MT",
      description: "Aprovao no concurso de Procurador Jurdico do Municpio de Alto Garas-MT.",
      icon: "Award",
      color: "bg-red-500"
    },
    {
      year: "2023",
      title: "Procurador em So Jos do Povo-MT",
      description: "Aprovao no concurso de Procurador Jurdico do Municpio de So Jos do Povo-MT, com posse em 2024.",
      icon: "CheckCircle",
      color: "bg-green-500"
    }
  ];

  return (
    <section className="py-20 bg-white">
      <div className="max-w-7xl mx-auto px-4 lg:px-6">
        <div className="text-center mb-16">
          <h2 className="font-lora text-3xl lg:text-4xl font-bold text-brand-primary mb-6">
            Trajetria Profissional
          </h2>
          <p className="text-lg text-text-secondary max-w-3xl mx-auto">
            Uma jornada de dedicao contnua ao aperfeioamento tcnico e ao atendimento 
            humanizado, sempre buscando a excelncia na prestao de servios jurdicos.
          </p>
        </div>

        <div className="relative">
          {/* Timeline line */}
          <div className="absolute left-4 lg:left-1/2 lg:transform lg:-translate-x-px top-0 bottom-0 w-0.5 bg-gradient-to-b from-brand-accent via-brand-accent/50 to-transparent"></div>

          <div className="space-y-12">
            {milestones?.map((milestone, index) => (
              <div
                key={milestone?.year}
                className={`relative flex items-start ${
                  index % 2 === 0 ? 'lg:flex-row' : 'lg:flex-row-reverse'
                }`}
              >
                {/* Timeline dot */}
                <div className="absolute left-4 lg:left-1/2 lg:transform lg:-translate-x-1/2 w-8 h-8 rounded-full bg-white border-4 border-brand-accent shadow-lg flex items-center justify-center z-10">
                  <div className={`w-3 h-3 rounded-full ${milestone?.color}`}></div>
                </div>

                {/* Content */}
                <div className={`ml-16 lg:ml-0 lg:w-1/2 ${
                  index % 2 === 0 ? 'lg:pr-12' : 'lg:pl-12'
                }`}>
                  <div className="bg-slate-50 rounded-xl p-6 shadow-sm hover:shadow-md transition-shadow duration-300">
                    <div className="flex items-center space-x-3 mb-4">
                      <div className={`w-12 h-12 ${milestone?.color} rounded-lg flex items-center justify-center shadow-sm`}>
                        <Icon name={milestone?.icon} size={24} color="white" />
                      </div>
                      <div>
                        <span className="inline-block px-3 py-1 bg-brand-accent/10 text-brand-accent text-sm font-semibold rounded-full">
                          {milestone?.year}
                        </span>
                      </div>
                    </div>
                    
                    <h3 className="font-lora text-xl font-semibold text-brand-primary mb-3">
                      {milestone?.title}
                    </h3>
                    
                    <p className="text-text-secondary leading-relaxed">
                      {milestone?.description}
                    </p>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default ProfessionalJourney;