import React, { useState } from 'react';
import Icon from '../../../components/AppIcon';

const FAQSection = () => {
  const [openFAQ, setOpenFAQ] = useState(null);

  const faqData = [
    {
      id: 1,
      question: "Como funciona a primeira consulta?",
      answer: `A primeira consulta  uma conversa detalhada onde analisamos sua situao especfica.\n\nDurante este encontro:\n Ouvimos sua questo com ateno e sigilo\n Explicamos as opes legais disponveis\n Definimos a melhor estratgia para seu caso\n Esclarecemos prazos e procedimentos\n\nO primeiro contato  sempre por meios digitais para sua maior comodidade, que geralmente  suficiente, para casos menos complexos, podendo ser realizada videoconferncia para melhor elucidao da lide, reservando-se atendimento presencial para casos especficos.`
    },
    {
      id: 2,
      question: "Quais documentos devo levar na consulta?",
      answer: `Os documentos variam conforme o tipo de questo, mas geralmente incluem:\n\n**Documentos Pessoais:**\n RG e CPF\n Comprovante de residncia\n\n**Documentos Especficos:**\n Contratos relacionados ao caso\n Correspondncias e e-mails\n Comprovantes de pagamento\n Fotos ou evidncias relevantes\n\nNo se preocupe se no tiver todos os documentos - podemos orient-lo sobre quais so necessrios.`
    },
    {
      id: 3,
      question: "Quanto tempo demora para resolver um processo?",
      answer: `O prazo varia significativamente conforme o tipo de caso e complexidade:\n\n**Casos Administrativos:** 30 a 90 dias\n**Questes Contratuais:** 6 meses a 2 anos\n**Processos Familiares:** 6 meses a 1 ano\n**Aes Consumeristas:** 3 meses a 1 ano\n\nSempre buscamos a resoluo mais rpida possvel, priorizando acordos quando vantajosos para voc. Os prazos apresentados levam por base nossa experincia em casos semelhantes, mas no so em absoluto uma promessa de resultado a curto prazo.`
    },
    {
      id: 4,
      question: " possvel resolver sem recorrer  justia?",
      answer: `Sim, priorizamos solues extrajudiciais sempre que possvel:\n\n**Negociao Direta:** Conversas com a outra parte\n**Mediao:** Processo assistido por mediador\n**Arbitragem:** Deciso por rbitro especializado\n**Acordos:** Solues consensuais\n\nEssas alternativas so mais rpidas, econmicas e preservam relacionamentos. Em ltimo caso, quando no h viabilidade em um acordo satisfatrio ao cliente, recomendamos o processo judicial, aps orientar sobre os riscos da demanda.`
      },
    {
      id: 5,
      question: "Como so calculados os honorrios?",
      answer: `Os honorrios so definidos de forma transparente e justa no momento da contratao, sempre respeitando a lei e a tabela de honorrios emitida pela Ordem dos Advogados do Brasil, seccional Mato Grosso.\n\nConsulta inicial: gratuita, para orientaes simples e gerais.\nConsulta com auditoria: valor pr-fixado em horas de trabalho, quando o cliente no busca contratar imediatamente nossos servios para demandas judidiciais, e sim obter orientao jurdica aprofundada sobre situao jurdica complexa que demande anlise pormenorizada e orientao preventiva de ao especfica.\nCausas sem proveito econmico: valor pr-fixado em conformidade com a tabela da OAB/MT e complexidade da causa.\nCausas com proveito econmico: percentual sobre o resultado til do processo.\nDependendo das peculiaridades do caso contrataes diversas ou hbridas podem ser realizadas, sempre de modo transparente, antes de iniciar qualquer trabalho, com orientao adequada sobre os custos envolvidos. No h surpresa.`
    },
    {
      id: 6,
      question: "Atende casos em outras cidades?",
      answer: `Nosso escritrio atende em todo o territrio nacional, em razo da virtualizao dos processos no Brasil, permitindo interposio, defesa e acompanhamento de qualquer lugar do mundo pela rede mundial de computadores.\n\nA maioria dos servios, desde a consulta inicial at a finalizao da demanda, podem ser efetivados por meios digitais, sem necessidade de comparecimento presencial do cliente ao escritrio ou frum, e sem necessidade de comparecimento fsico de um advogado ao local em que tramita o processo.\n\nSe houver necessidade de deslocamento, as taxas so previstas em contrato. Servios praticados na cidade de Rondonpolis-MT no tem custo de deslocamento ou diligncia. Em casos especficos o cliente pode ser orientado a procurar um profissional em sua localidade, se houver necessidade de comparecimento presencial constante em rgos pblicos ou comarcas, de modo que o custo de deslocamento se torne excessivamente expressivo.`
    }
  ];

  const toggleFAQ = (id) => {
    setOpenFAQ(openFAQ === id ? null : id);
  };

  return (
    <section className="py-16 lg:py-20 bg-muted/30">
      <div className="max-w-4xl mx-auto px-4 lg:px-6">
        {/* Header */}
        <div className="text-center mb-12">
          <div className="inline-flex items-center justify-center w-16 h-16 bg-gradient-to-br from-brand-accent to-brand-accent/80 rounded-full mb-6">
            <Icon name="HelpCircle" size={28} color="white" />
          </div>
          <h2 className="font-lora font-semibold text-3xl lg:text-4xl text-brand-primary mb-4">
            Perguntas Frequentes
          </h2>
          <p className="text-lg text-text-secondary font-inter max-w-2xl mx-auto">
            Esclarecemos as dvidas mais comuns sobre nossos servios jurdicos
          </p>
        </div>

        {/* FAQ Accordion */}
        <div className="space-y-4">
          {faqData?.map((faq) => (
            <div
              key={faq?.id}
              className="bg-white rounded-xl border border-border shadow-sm overflow-hidden"
            >
              <button
                onClick={() => toggleFAQ(faq?.id)}
                className="w-full px-6 py-5 text-left flex items-center justify-between hover:bg-muted/20 transition-colors duration-200"
              >
                <h3 className="font-inter font-medium text-lg text-brand-primary pr-4">
                  {faq?.question}
                </h3>
                <Icon
                  name={openFAQ === faq?.id ? "ChevronUp" : "ChevronDown"}
                  size={20}
                  className={`text-brand-accent transition-transform duration-200 flex-shrink-0 ${
                    openFAQ === faq?.id ? 'rotate-180' : ''
                  }`}
                />
              </button>
              
              <div
                className={`transition-all duration-300 ease-in-out ${
                    openFAQ === faq?.id
                      ? 'max-h-[1000px] opacity-100' : 'max-h-0 opacity-0 overflow-hidden'
                  }`}
              >
                <div className="px-6 pb-5 border-t border-border/50">
                  <div className="pt-4">
                    {faq?.answer?.split('\n')?.map((line, index) => (
                      <React.Fragment key={index}>
                        {line && (
                          <p className={`text-text-secondary font-inter leading-relaxed ${
                            line?.startsWith('**') && line?.endsWith('**')
                              ? 'font-medium text-brand-secondary mt-3 mb-1' : line?.startsWith('')
                              ? 'ml-4 mb-1' :'mb-2'
                          }`}>
                            {line?.replace(/\*\*/g, '')}
                          </p>
                        )}
                        {!line && <br />}
                      </React.Fragment>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>

        {/* CTA */}
        <div className="text-center mt-12">
          <p className="text-text-secondary font-inter mb-6">
            Precisa de outras orientaes?
          </p>
          <div className="flex flex-col sm:flex-row gap-4 justify-center">
            <button
              onClick={() => window.open('https://wa.me/5566999111314', '_blank')}
              className="inline-flex items-center justify-center px-6 py-3 bg-brand-accent hover:bg-brand-accent/90 text-white font-inter font-medium rounded-lg transition-colors durao-200"
            >
              <Icon name="MessageCircle" size={18} className="mr-2" />
              Perguntar no WhatsApp
            </button>
            <button
              onClick={() => window.location.href = 'tel:+5566999111314'}
              className="inline-flex items-center justify-center px-6 py-3 border border-brand-accent text-brand-accent hover:bg-brand-accent hover:text-white font-inter font-medium rounded-lg transition-colors durao-200"
            >
              <Icon name="Phone" size={18} className="mr-2" />
              (66) 99911-1314
            </button>
          </div>
        </div>
      </div>
    </section>
  );
};

export default FAQSection;