import React, { useEffect, useState } from 'react';
import { useSearchParams } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import Header from '../../components/ui/Header';
import PracticeAreaHero from './components/PracticeAreaHero';
import ServiceBreakdown from './components/ServiceBreakdown';
import ContextualFAQ from './components/ContextualFAQ';
import CaseStudyApproach from './components/CaseStudyApproach';
import PracticeAreaSelector from './components/PracticeAreaSelector';
import ContactCTA from './components/ContactCTA';

const PaginaAreaDeAtuacao = () => {
  const [searchParams] = useSearchParams();
  const [currentArea, setCurrentArea] = useState('civil');

  useEffect(() => {
    const area = searchParams?.get('area');
    if (area && ['civil', 'consumer', 'family', 'business']?.includes(area)) {
      setCurrentArea(area);
    }
  }, [searchParams]);

  const handleWhatsAppClick = () => {
    window.open('https://wa.me/5566999111314', '_blank');
  };

  const handleCallClick = () => {
    window.location.href = 'tel:+5566999111314';
  };

  const getPageTitle = () => {
    const titles = {
      civil: 'Direito Civil - Marcelo Baa Advocacia',
      consumer: 'Direito do Consumidor - Marcelo Baa Advocacia',
      family: 'Direito de Famlia - Marcelo Baa Advocacia',
      business: 'Direito Empresarial - Marcelo Baa Advocacia'
    };
    return titles?.[currentArea] || titles?.civil;
  };

  const getPageDescription = () => {
    const descriptions = {
      civil: 'Especialista em Direito Civil em Rondonpolis/MT. Contratos, responsabilidade civil, direitos reais e questes patrimoniais com atendimento personalizado.',
      consumer: 'Defesa do Consumidor em Rondonpolis/MT. Proteo contra prticas abusivas, vcios de produtos, servios bancrios e direitos do consumidor.',
      family: 'Direito de Famlia em Rondonpolis/MT. Divrcio, guarda, penso alimentcia e questes familiares com atendimento humanizado e discreto.',
      business: 'Direito Empresarial em Rondonpolis/MT. Consultoria jurdica para empresas, contratos comerciais, recuperao judicial e compliance.'
    };
    return descriptions?.[currentArea] || descriptions?.civil;
  };

  return (
    <>
      <Helmet>
        <title>{getPageTitle()}</title>
        <meta name="description" content={getPageDescription()} />
        <meta name="keywords" content={`${currentArea === 'civil' ? 'direito civil, contratos, responsabilidade civil' : currentArea === 'consumer' ? 'direito consumidor, defesa consumidor, vcios produtos' : currentArea === 'family' ? 'direito famlia, divrcio, guarda, penso' : 'direito empresarial, consultoria jurdica, contratos comerciais'}, advogado rondonpolis, marcelo baa`} />
        <meta property="og:title" content={getPageTitle()} />
        <meta property="og:description" content={getPageDescription()} />
        <meta property="og:image" content="/assets/images/logo-512x512.png" />
        <meta name="twitter:image" content="/assets/images/logo-512x512.png" />
        <meta property="og:type" content="website" />
          <meta property="og:url" content={`https://marcelobaia.adv.br/pagina-area-de-atuacao?area=${currentArea}`} />
          <link rel="canonical" href={`https://marcelobaia.adv.br/pagina-area-de-atuacao?area=${currentArea}`} />
        <script type="application/ld+json">
          {JSON.stringify({
            "@context": "https://schema.org",
            "@type": "LegalService",
            name: "Marcelo Baa Advocacia",
            telephone: "+55 66 99911-1314",
            address: {
              "@type": "PostalAddress",
              streetAddress: "Rua Tiradentes, 1888",
              addressLocality: "Rondonpolis",
              addressRegion: "MT",
              postalCode: "78700-028",
              addressCountry: "BR",
            },
            openingHoursSpecification: {
              "@type": "OpeningHoursSpecification",
              description:
                "Atendimento presencial mediante agendamento. Atendimento por telefone entre as 7:00h e 17:00h de segunda-feira  sexta-feira e via WhatsApp 24h por dia 7 dias por semana.",
            },
          })}
        </script>
      </Helmet>
      <div className="min-h-screen bg-white">
        <Header />
        
        <main className="pt-16 lg:pt-20">
          <PracticeAreaHero 
            practiceArea={currentArea}
            onWhatsAppClick={handleWhatsAppClick}
            onCallClick={handleCallClick}
          />
          
          <ServiceBreakdown practiceArea={currentArea} />
          
          <CaseStudyApproach practiceArea={currentArea} />
          
          <ContextualFAQ practiceArea={currentArea} />
          
          <ContactCTA practiceArea={currentArea} />
          
          <PracticeAreaSelector currentArea={currentArea} />
        </main>

        {/* Footer */}
        <footer className="bg-slate-900 text-white py-12">
          <div className="max-w-7xl mx-auto px-4 lg:px-6">
            <div className="grid md:grid-cols-3 gap-8">
              {/* Contact Info */}
              <div>
                <h3 className="font-lora font-semibold text-xl mb-4">Contato</h3>
                <div className="space-y-3">
                  <p className="font-inter text-slate-300">
                     Rua Tiradentes, 1888  Centro, Rondonpolis-MT  CEP 78.700-028
                  </p>
                  <p className="font-inter text-slate-300">
                     <a href="tel:+5566999111314" className="text-slate-300">(66) 99911-1314</a>
                  </p>
                  <p className="font-inter text-slate-300">
                     <a href="https://wa.me/5566999111314" target="_blank" rel="noopener noreferrer" className="text-slate-300">(66) 99911-1314</a>
                  </p>
                  <p className="font-inter text-slate-300">
                     <a href="mailto:contato@marcelobaia.adv.br" className="text-slate-300">contato@marcelobaia.adv.br</a>
                  </p>
                </div>
              </div>

              {/* Practice Areas */}
              <div>
                <h3 className="font-lora font-semibold text-xl mb-4">reas de Atuao</h3>
                <div className="space-y-2">
                  <p className="font-inter text-slate-300">Direito Civil</p>
                  <p className="font-inter text-slate-300">Direito do Consumidor</p>
                  <p className="font-inter text-slate-300">Direito de Famlia</p>
                  <p className="font-inter text-slate-300">Direito Empresarial</p>
                </div>
              </div>

              {/* Legal */}
              <div>
                <h3 className="font-lora font-semibold text-xl mb-4">Informaes Legais</h3>
                <div className="space-y-2">
                  <p className="font-inter text-slate-300 text-sm">
                    OAB/MT 14.159-B
                  </p>
                  <p className="font-inter text-slate-300 text-sm">
                    Atendimento presencial mediante agendamento
                  </p>
                  <p className="font-inter text-slate-300 text-sm">
                    Atendimento por telefone entre as 7:00h e 17:00h de segunda-feira  sexta-feira e via WhatsApp 24h por dia 7 dias por semana
                  </p>
                </div>
              </div>
            </div>

            <div className="border-t border-slate-700 mt-8 pt-8 text-center">
              <p className="font-inter text-slate-400 text-sm">
                 {new Date()?.getFullYear()} Marcelo Baa Advocacia. Todos os direitos reservados.
              </p>
            </div>
          </div>
        </footer>
      </div>
    </>
  );
};

export default PaginaAreaDeAtuacao;