import React, { useState, useEffect, useMemo } from 'react';
import { Helmet } from 'react-helmet';
import Header from '../../components/ui/Header';
import SearchBar from './components/SearchBar';
import CategoryFilter from './components/CategoryFilter';
import FAQAccordion from './components/FAQAccordion';
import PopularQuestions from './components/PopularQuestions';
import ContactCTA from './components/ContactCTA';
import Icon from '../../components/AppIcon';

const PerguntasFrequentes = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [activeCategory, setActiveCategory] = useState('todos');
  const [currentLanguage, setCurrentLanguage] = useState('pt-BR');

  // Load language preference on mount
  useEffect(() => {
    const savedLanguage = localStorage.getItem('language') || 'pt-BR';
    setCurrentLanguage(savedLanguage);
  }, []);

  // Mock FAQ data
  const faqData = [
    {
      id: 1,
      question: "Como funciona a primeira consulta?",
      answer: `O contato inicial  uma conversa onde voc pode expor sua situao jurdica sem compromisso. Durante este encontro, analisamos seu caso, esclarecemos suas dvidas principais e apresentamos as possveis estratgias legais.\n\nO contato inicial tem durao mdia de 60 minutos e pode ser realizado presencialmente em nosso escritrio em Rondonpolis ou por videoconferncia. Ao final, voc receber uma orientao clara sobre os prximos passos e os custos envolvidos.`,
      category: "Primeiros Passos",
      categoryId: "primeiros-passos",
      isPopular: true,
        relatedLinks: [
          { title: "Agendar atendimento", url: "/contato-consulta" },
          { title: "Sobre Ns", url: "/sobre-marcelo-baia" }
        ]
    },
    {
      id: 2,
      question: "Quais documentos preciso enviar?",
      answer: `Os documentos necessrios variam conforme o tipo de caso, mas geralmente incluem:\n\n Documentos pessoais (RG, CPF, comprovante de residncia)\n Contratos relacionados ao caso\n Correspondncias e notificaes recebidas\n Comprovantes de pagamento ou recibos\n Laudos, percias ou documentos tcnicos\n Procuraes anteriores (se houver).\n\nAntes da consulta, enviamos uma lista especfica dos documentos necessrios para seu tipo de caso, otimizando nosso tempo de atendimento.`,
      category: "Primeiros Passos",
      categoryId: "primeiros-passos",
      isPopular: true,
        relatedLinks: [
          { title: "reas de Atuao", url: "/areas-de-atuacao" }
        ]
    },
    {
      id: 3,
      question: "Quanto tempo demora um processo?",
      answer: `O tempo de durao varia significativamente conforme o tipo de processo e a complexidade do caso:\n\n Processos administrativos: 3 a 12 meses\n Aes de cobrana: 6 a 18 meses\n Processos trabalhistas: 12 a 24 meses\n Aes de famlia: 6 a 36 meses\n Processos criminais: 12 meses a vrios anos\n\nEstes prazos so estimativas baseadas na experincia prtica. Durante a consulta, fornecemos uma previso mais precisa considerando as particularidades do seu caso e a comarca onde ser processado.`,
      category: "Processo Legal",
      categoryId: "processo-legal",
      isPopular: true
    },
    {
      id: 4,
      question: "Como so calculados os honorrios advocatcios?",
      answer: `Os honorrios so calculados considerando diversos fatores:\n\n Complexidade do caso\n Tempo estimado de dedicao\n Valor da causa (quando aplicvel)\n Urgncia do atendimento\n Modalidade de cobrana escolhida\n\nOferecemos trs modalidades:\n1. Honorrios fixos: valor determinado no incio\n2. Honorrios por hora: cobrana pelo tempo efetivamente trabalhado\n3. Honorrios de xito: percentual sobre o resultado obtido.\n\nTodos os valores so transparentes e formalizados em contrato, sem surpresas ou custos ocultos.`,
      category: "Custos e Honorrios",
      categoryId: "custos-honorarios",
      isPopular: false
    },
    {
      id: 5,
      question: "Posso acompanhar o andamento do meu processo?",
      answer: `Os Tribunais de Justia fornecem mecanismos de consulta pblica de processos (caso no haja sigilo) disponveis 24h por dia, 7 dias por semana. Qualquer dvida pode ser obtida de maneira rpida por WhatsApp. Em casos mais complexos sero marcadas reunies e envio de relatrios.`,
      category: "Processo Legal",
      categoryId: "processo-legal",
      isPopular: false
    },
    {
      id: 6,
      question: "Atende casos em outras cidades alm de Rondonpolis?",
      answer: `Sim, atendemos clientes em toda a regio de Rondonpolis e cidades vizinhas. Para casos em outras comarcas, avaliamos a viabilidade considerando:\n\n Complexidade do caso\n Necessidade de deslocamento\n Possibilidade de atendimento remoto\n Custos adicionais de deslocamento.\n\nMuitos atendimentos podem ser realizados por videoconferncia, especialmente consultas iniciais e acompanhamentos. Para audincias e atos presenciais obrigatrios, organizamos a agenda para otimizar custos e tempo.`,
      category: "Atendimento",
      categoryId: "atendimento",
      isPopular: false
    },
    {
      id: 7,
      question: "Qual a diferena entre Direito Civil e Direito do Consumidor?",
      answer: `O Direito Civil regula as relaes entre pessoas fsicas e jurdicas em geral, incluindo:\n Contratos diversos\n Responsabilidade civil\n Direitos reais (propriedade)\n Famlia e sucesses.\n\nO Direito do Consumidor  uma especializao que protege especificamente as relaes de consumo:\n Compra e venda de produtos\n Prestao de servios\n Publicidade enganosa\n Cobrana abusiva\n Defeitos e vcios\n\nAmbas as reas se complementam, e muitas vezes um caso pode envolver aspectos dos dois ramos do direito.`,
      category: "Direito Civil",
      categoryId: "direito-civil",
      isPopular: false,
      relatedLinks: [
        { title: "Direito Civil", url: "/pagina-area-de-atuacao" },
        { title: "Direito do Consumidor", url: "/pagina-area-de-atuacao" }
      ]
    },
    {
      id: 8,
      question: "Como funciona a penso alimentcia?",
      answer: `A penso alimentcia  uma obrigao legal que visa garantir o sustento de quem no pode prover suas prprias necessidades. Principais aspectos:\n\n Pode ser para filhos menores, cnjuges ou pais idosos\n Valor baseado na necessidade de quem recebe e possibilidade de quem paga\n Inclui alimentao, moradia, sade, educao e lazer\n Pode ser revista a qualquer tempo mediante mudana de circunstncias\n Descumprimento pode levar  priso civil.\n\nO processo de fixao considera a renda do alimentante, as necessidades do alimentado e o padro de vida anterior da famlia.`,
      category: "Direito de Famlia",
      categoryId: "direito-familia",
      isPopular: false,
      relatedLinks: [
        { title: "Direito de Famlia", url: "/pagina-area-de-atuacao" }
      ]
    },
    {
      id: 9,
      question: "Minha empresa precisa de assessoria jurdica permanente?",
      answer: `A assessoria jurdica permanente  recomendada para empresas que:\n\n Tm contratos frequentes com fornecedores/clientes\n Lidam com questes trabalhistas regularmente\n Precisam de orientao em decises estratgicas\n Querem prevenir problemas legais\n Tm faturamento acima de R$ 500.000/ano\n\nPara empresas menores, oferecemos consultoria pontual e planto jurdico mensal. A assessoria permanente inclui:\n Anlise de contratos\n Orientao trabalhista\n Compliance bsico\n Atendimento prioritrio\n Desconto em demandas judiciais`,
      category: "Direito Empresarial",
      categoryId: "direito-empresarial",
      isPopular: false,
      relatedLinks: [
        { title: "Direito Empresarial", url: "/pagina-area-de-atuacao" }
      ]
    },
    {
      id: 10,
      question: "O que fazer quando recebo uma cobrana indevida?",
      answer: `Ao receber uma cobrana indevida, siga estes passos:\n\n1. **No pague imediatamente** - Analise a cobrana com calma\n2. **Rena documentos** - Contratos, comprovantes de pagamento, correspondncias\n3. **Conteste formalmente** - Por escrito, preferencialmente por email\n4. **Guarde comprovantes** - De todas as comunicaes realizadas\n5. **Procure orientao jurdica** - Se a empresa insistir na cobrana\n\nVoc tem direito a:\n Cancelamento da cobrana\n Excluso do nome dos rgos de proteo\n Indenizao por danos morais (em casos especficos)\n Devoluo em dobro do valor pago indevidamente`,
      category: "Direito do Consumidor",
      categoryId: "direito-consumidor",
      isPopular: false,
      relatedLinks: [
        { title: "Direito do Consumidor", url: "/pagina-area-de-atuacao" }
      ]
    }
  ];

  // Categories with counts
  const categories = [
    { id: 'todos', name: 'Todas', icon: 'Grid3X3', count: faqData?.length },
    { id: 'primeiros-passos', name: 'Primeiros Passos', icon: 'Play', count: faqData?.filter(faq => faq?.categoryId === 'primeiros-passos')?.length },
    { id: 'custos-honorarios', name: 'Custos', icon: 'DollarSign', count: faqData?.filter(faq => faq?.categoryId === 'custos-honorarios')?.length },
    { id: 'processo-legal', name: 'Processo Legal', icon: 'FileText', count: faqData?.filter(faq => faq?.categoryId === 'processo-legal')?.length },
    { id: 'direito-civil', name: 'Direito Civil', icon: 'Scale', count: faqData?.filter(faq => faq?.categoryId === 'direito-civil')?.length },
    { id: 'direito-familia', name: 'Direito de Famlia', icon: 'Users', count: faqData?.filter(faq => faq?.categoryId === 'direito-familia')?.length },
    { id: 'direito-empresarial', name: 'Direito Empresarial', icon: 'Building', count: faqData?.filter(faq => faq?.categoryId === 'direito-empresarial')?.length },
    { id: 'direito-consumidor', name: 'Direito do Consumidor', icon: 'ShoppingCart', count: faqData?.filter(faq => faq?.categoryId === 'direito-consumidor')?.length }
  ];

  // Popular questions (first 5 popular ones)
  const popularQuestions = faqData?.filter(faq => faq?.isPopular)?.slice(0, 5);

  // Filter FAQs based on search and category
  const filteredFAQs = useMemo(() => {
    let filtered = faqData;

    // Filter by category
    if (activeCategory !== 'todos') {
      filtered = filtered?.filter(faq => faq?.categoryId === activeCategory);
    }

    // Filter by search term
    if (searchTerm) {
      const searchLower = searchTerm?.toLowerCase();
      filtered = filtered?.filter(faq => 
        faq?.question?.toLowerCase()?.includes(searchLower) ||
        faq?.answer?.toLowerCase()?.includes(searchLower) ||
        faq?.category?.toLowerCase()?.includes(searchLower)
      );
    }

    return filtered;
  }, [searchTerm, activeCategory]);

  const handleSearchChange = (term) => {
    setSearchTerm(term);
  };

  const handleClearSearch = () => {
    setSearchTerm('');
  };

  const handleCategoryChange = (categoryId) => {
    setActiveCategory(categoryId);
    setSearchTerm(''); // Clear search when changing category
  };

  const handlePopularQuestionClick = (questionId) => {
    // Find the question and scroll to it
    const element = document.getElementById(`faq-${questionId}`);
    if (element) {
      element?.scrollIntoView({ behavior: 'smooth', block: 'center' });
    }
  };

  return (
    <>
      <Helmet>
        <title>FAQ - Perguntas Frequentes | Marcelo Baa Advocacia</title>
        <meta name="description" content="Encontre respostas para as principais dvidas jurdicas. FAQ completo sobre direito civil, consumidor, famlia e empresarial em Rondonpolis-MT." />
        <meta name="keywords" content="FAQ jurdico, perguntas frequentes direito, dvidas advocacia, Rondonpolis MT, consulta jurdica" />
          <link rel="canonical" href="https://marcelobaia.adv.br/perguntas-frequentes" />
        <meta property="og:title" content="FAQ - Perguntas Frequentes | Marcelo Baa Advocacia" />
        <meta property="og:description" content="Encontre respostas para as principais dvidas jurdicas. FAQ completo sobre direito civil, consumidor, famlia e empresarial." />
        <meta property="og:image" content="/assets/images/logo-512x512.png" />
        <meta name="twitter:image" content="/assets/images/logo-512x512.png" />
        <meta property="og:type" content="website" />
          <meta property="og:url" content="https://marcelobaia.adv.br/perguntas-frequentes" />
        <script type="application/ld+json">
          {JSON.stringify({
            "@context": "https://schema.org",
            "@type": "LegalService",
            name: "Marcelo Baa Advocacia",
            telephone: "+55 66 99911-1314",
            address: {
              "@type": "PostalAddress",
              streetAddress: "Rua Tiradentes, 1888",
              addressLocality: "Rondonpolis",
              addressRegion: "MT",
              postalCode: "78700-028",
              addressCountry: "BR",
            },
            openingHoursSpecification: {
              "@type": "OpeningHoursSpecification",
              description:
                "Atendimento presencial mediante agendamento. Atendimento por telefone entre as 7:00h e 17:00h de segunda-feira  sexta-feira e via WhatsApp 24h por dia 7 dias por semana.",
            },
          })}
        </script>
      </Helmet>
      <div className="min-h-screen bg-background">
        <Header />
        
        {/* Hero Section */}
        <section className="pt-20 lg:pt-24 pb-12 lg:pb-16 bg-gradient-to-br from-brand-primary via-brand-secondary to-brand-primary">
          <div className="max-w-7xl mx-auto px-4 lg:px-6">
            <div className="text-center text-white">
              <div className="flex items-center justify-center space-x-3 mb-6">
                <div className="w-12 h-12 lg:w-16 lg:h-16 bg-white/20 rounded-xl flex items-center justify-center">
                  <Icon name="HelpCircle" size={28} />
                </div>
                <div className="text-left">
                  <h1 className="font-lora font-bold text-3xl lg:text-5xl leading-tight">
                    Perguntas Frequentes
                  </h1>
                  <p className="text-white/80 text-lg lg:text-xl font-inter">
                    Respostas claras para suas dvidas jurdicas
                  </p>
                </div>
              </div>
              
              <p className="text-white/90 text-lg lg:text-xl max-w-3xl mx-auto mb-8 leading-relaxed">
                Encontre respostas imediatas para as principais questes jurdicas ou entre em contato 
                para esclarecimentos personalizados sobre seu caso especfico.
              </p>
              
              {/* Search Bar */}
              <div className="max-w-4xl mx-auto">
                <SearchBar
                  searchTerm={searchTerm}
                  onSearchChange={handleSearchChange}
                  onClearSearch={handleClearSearch}
                />
              </div>
            </div>
          </div>
        </section>

        {/* Category Filter */}
        <section className="py-8 lg:py-12 bg-white border-b border-border">
          <div className="max-w-7xl mx-auto px-4 lg:px-6">
            <CategoryFilter
              categories={categories}
              activeCategory={activeCategory}
              onCategoryChange={handleCategoryChange}
            />
          </div>
        </section>

        {/* Main Content */}
        <section className="py-12 lg:py-20">
          <div className="max-w-7xl mx-auto px-4 lg:px-6">
            <div className="grid lg:grid-cols-3 gap-8 lg:gap-12">
              {/* FAQ Content */}
              <div className="lg:col-span-2">
                <div className="mb-8">
                  <div className="flex items-center justify-between mb-6">
                    <h2 className="font-lora font-semibold text-2xl lg:text-3xl text-text-primary">
                      {activeCategory === 'todos' ? 'Todas as Perguntas' : categories?.find(cat => cat?.id === activeCategory)?.name}
                    </h2>
                    <span className="text-text-secondary font-inter">
                      {filteredFAQs?.length} {filteredFAQs?.length === 1 ? 'pergunta' : 'perguntas'}
                    </span>
                  </div>
                  
                  {searchTerm && (
                    <div className="mb-6 p-4 bg-accent/10 rounded-lg border border-accent/20">
                      <p className="text-text-primary font-inter">
                        Mostrando resultados para: <span className="font-semibold">"{searchTerm}"</span>
                      </p>
                    </div>
                  )}
                </div>

                <FAQAccordion faqs={filteredFAQs} searchTerm={searchTerm} />
              </div>

              {/* Sidebar */}
              <div className="space-y-8">
                {/* Popular Questions */}
                <PopularQuestions
                  questions={popularQuestions}
                  onQuestionClick={handlePopularQuestionClick}
                />

                {/* Quick Stats */}
                <div className="bg-white rounded-2xl border border-border shadow-sm p-6">
                  <h3 className="font-lora font-semibold text-lg text-text-primary mb-4">
                    Central de Ajuda
                  </h3>
                  <div className="space-y-4">
                    <div className="flex items-center space-x-3">
                      <div className="w-8 h-8 bg-brand-accent/10 rounded-lg flex items-center justify-center">
                        <Icon name="FileText" size={16} className="text-brand-accent" />
                      </div>
                      <div>
                        <p className="font-inter font-medium text-text-primary">{faqData?.length} Perguntas</p>
                        <p className="text-text-secondary text-sm">Respondidas</p>
                      </div>
                    </div>
                    
                    <div className="flex items-center space-x-3">
                      <div className="w-8 h-8 bg-brand-accent/10 rounded-lg flex items-center justify-center">
                        <Icon name="Clock" size={16} className="text-brand-accent" />
                      </div>
                      <div>
                        <p className="font-inter font-medium text-text-primary">24h</p>
                        <p className="text-text-secondary text-sm">Resposta mdia</p>
                      </div>
                    </div>
                    
                    <div className="flex items-center space-x-3">
                      <div className="w-8 h-8 bg-brand-accent/10 rounded-lg flex items-center justify-center">
                        <Icon name="Users" size={16} className="text-brand-accent" />
                      </div>
                      <div>
                        <p className="font-inter font-medium text-text-primary">500+</p>
                        <p className="text-text-secondary text-sm">Clientes atendidos</p>
                      </div>
                    </div>
                  </div>
                </div>

                {/* Practice Areas Quick Links */}
                <div className="bg-white rounded-2xl border border-border shadow-sm p-6">
                  <h3 className="font-lora font-semibold text-lg text-text-primary mb-4">
                    reas de Atuao
                  </h3>
                  <div className="space-y-2">
                    {[
                      { name: 'Direito Civil', icon: 'Scale', url: '/pagina-area-de-atuacao' },
                      { name: 'Direito do Consumidor', icon: 'ShoppingCart', url: '/pagina-area-de-atuacao' },
                      { name: 'Direito de Famlia', icon: 'Users', url: '/pagina-area-de-atuacao' },
                      { name: 'Direito Empresarial', icon: 'Building', url: '/pagina-area-de-atuacao' }
                    ]?.map((area) => (
                      <a
                        key={area?.name}
                        href={area?.url}
                        className="flex items-center space-x-3 p-3 rounded-lg hover:bg-muted/30 transition-colors duration-200 group"
                      >
                        <Icon name={area?.icon} size={16} className="text-brand-accent" />
                        <span className="text-text-primary font-inter text-sm group-hover:text-brand-accent transition-colors duration-200">
                          {area?.name}
                        </span>
                        <Icon name="ArrowRight" size={14} className="text-text-secondary group-hover:text-brand-accent group-hover:translate-x-1 transition-all duration-200 ml-auto" />
                      </a>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>

        {/* Contact CTA Section */}
        <section className="py-12 lg:py-20 bg-muted/30">
          <div className="max-w-4xl mx-auto px-4 lg:px-6">
            <ContactCTA />
          </div>
        </section>

        {/* Footer CTA */}
        <section className="py-12 lg:py-16 bg-gradient-to-r from-brand-primary to-brand-secondary">
          <div className="max-w-4xl mx-auto px-4 lg:px-6 text-center text-white">
            <div className="mb-6">
              <Icon name="MessageCircle" size={48} className="mx-auto mb-4 opacity-80" />
              <h2 className="font-lora font-bold text-2xl lg:text-3xl mb-4">
                Ainda tem dvidas?
              </h2>
              <p className="text-white/90 text-lg lg:text-xl leading-relaxed">
                Nossa equipe est pronta para esclarecer suas questes jurdicas 
                e orientar sobre os melhores caminhos para seu caso.
              </p>
            </div>
            
            <div className="flex flex-col sm:flex-row gap-4 justify-center">
              <button
                onClick={() => window.open('https://wa.me/5566999111314', '_blank')}
                className="flex items-center justify-center space-x-3 px-8 py-4 bg-white text-brand-primary rounded-xl font-inter font-semibold hover:bg-white/90 transition-all duration-300 shadow-lg hover:shadow-xl"
              >
                <Icon name="MessageCircle" size={20} />
                <span>Conversar no WhatsApp</span>
              </button>
              
              <button
                onClick={() => window.location.href = 'tel:+5566999111314'}
                className="flex items-center justify-center space-x-3 px-8 py-4 bg-white/10 text-white border border-white/20 rounded-xl font-inter font-semibold hover:bg-white/20 transition-all duration-300"
              >
                <Icon name="Phone" size={20} />
                <span>Ligar Agora</span>
              </button>
            </div>
          </div>
        </section>
      </div>
    </>
  );
};

  export default PerguntasFrequentes;
